
export default {
    domain: 'https://phudino-59e01.firebaseio.com',
    send : function(path = '', method = 'GET', body = null) {
        const configRequest = {
            method: method,
            headers: {
                'Content-Type': 'application/json',
            }
        }

        if(body){
            configRequest.body = JSON.stringify(body);
        }

        return fetch(this.domain + '/'  + path + '.json', configRequest)
        .then(response => response.json());
    }
}