import FireBaseService from '@/services/FireBaseService.js'

export default {
    all: function(){
        return FireBaseService.send('posts')
        .then(data => {
            let posts = [];
            for (const id in data) {
                data[id].id = id;
                posts.push(data[id]);
            }
            return posts;
        })
        .catch((error) => {
            console.error('Error:', error);
            return [];
        });
    },
    create: function(title, subtitle, content){
        let id = 'p-' + new Date().getTime() + Math.floor(Math.random() * 100) + 1;
        return FireBaseService.send('posts/' + id, 'PUT', {
            title: title,
            subtitle: subtitle,
            content: content,
            url: '/post/'+ id,
            time: new Date().getTime()
        })
        .then(post => {
            post.id = id;
            return post;
        })
        .catch((error) => {
            console.error('Error:', error);
            return null;
        });
    }
}