<template>
    <div class="create-post">
        <page-title>
            Create Post
        </page-title>
        <div class="row">
            <div class="col-9">
                <div class="card ">
                    <div class="card-body p-4">
                        <div v-if="message" class="alert alert-success" role="alert">
                            {{message}}
                        </div>
                        <form>
                            <div class="mb-3">
                                <label for="titleInput" class="form-label">Title</label>
                                <input type="text" class="form-control" id="titleInput" v-model="title">
                            </div>
                            <div class="mb-3">
                                <label for="subtitleInput" class="form-label">Subtitle</label>
                                <input type="text" class="form-control" id="subtitleInput" v-model="subtitle">
                            </div>
                            <div class="mb-3">
                                <label for="contentInput" class="form-label">Content</label>
                                <textarea class="form-control" id="contentInput" rows="3" v-model="content"></textarea>
                            </div>
                            <button type="button" @click="createPost" class="btn btn-primary">Save</button>
                        </form>
                    </div>
                </div>
            </div>
            <div class="col-3">
                <ul>
                    <li class="post-item" v-for="post in posts" :key="post.id">
                        <div><a :href="post.url">{{post.title}}</a></div>
                        <div class="post-item__time"><i>{{new Date(post.time).toLocaleString()}}</i></div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
// @ is an alias to /src
import PageTitle from '@/views/components/ui/PageTitle.vue';
import PostService from '@/services/PostService.js';

export default {
  components: { PageTitle },
  data(){
    return {
        posts: [],
        title: '',
        subtitle: '',
        content: '',
        message: ''
    };
  },
  mounted: function(){
    this.loadPost();
  },
  methods: {
    loadPost: function(){
        PostService.all().then( data => {
            this.posts = data;
        });
    },
    createPost: function(){
        this.message = '';
        PostService.create(
            this.title, this.subtitle, this.content
        ).then( () => {
            this.title = '';
            this.subtitle = '';
            this.content = '';
            this.message = 'Success';
            this.loadPost();
        });
    }
  },
  
}
</script>

<style scoped>
ul{
    padding-left:0px;
}
.form-create-post{
    border-right: 1px solid rgba(128, 128, 128, 0.26);
}
.post-item{
    padding:5px;
    list-style: none;
}

.post-item__time{
    text-align: right;
    font-size: 0.6rem;
}
</style>